    background-color: #fff;
    color:            #000;
    &:hover, &:active {
      background-color: #000;
      color:            #fff;
    }
  }

  section.sn-alt.sn-light,
  section.sn-dark {
    .uk-button-default {
      background-color: #fff;
      color:            #000;
      &:hover, &:active {
        background-color: #000;
        color:            #fff;
      }
    }
    .uk-button-primary {
      background-color: $sn-dev-color;
      color:            #000;
      &:hover, &:active {
